import React, {Component} from 'react';
import AppButton from "src/components/App/Button/AppButton";
import AppRow from "src/components/App/Row/AppRow";
import AppTitle from "src/components/App/Title/AppTitle";
import CollectionList from "src/components/Collection/List/CollectionList";
import {ReactComponent as AddIcon} from "src/assets/icons/add.svg";

import {connect} from "react-redux";

import classes from "./NasaCollectionList.module.scss";

class NasaCollectionList extends Component {
    render() {
        return (
            <>
                <AppRow className={classes.header}>
                    <AppTitle>NASA Collection</AppTitle>
                    <AppButton icon={AddIcon} to={'/search'} primary={1}>
                        Add new item
                    </AppButton>
                </AppRow>
                <CollectionList items={this.props.items}/>
            </>
        );
    }
}

const mapStateToProps = state => ({
    items: state
});

export default connect(mapStateToProps)(NasaCollectionList);
